.Iam {
  color: #999;
  font: normal 40px/50px Montserrat, sans-serif;
}
.Iam p {
  float: left;
  height: 50px;
  margin-right: 0.3em;
}
.Iam b {
  position: relative;
  float: left;
  height: 50px;
  overflow: hidden;
}

.Iam .innerIam {
  display: inline-block;
  position: relative;
  top: 0;
  left: 0;
  color: #e74c3c;
  white-space: nowrap;

  /*animation*/
  -webkit-animation: move 7s;
  animation: move 7s;
  /*animation-iteration-count*/
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  /*animation-delay*/
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
}
@keyframes move {
  0% {
    top: 0px;
  }
  20% {
    top: -50px;
  }
  40% {
    top: -100px;
  }
  60% {
    top: -150px;
  }
  80% {
    top: -200px;
  }
}

@-webkit-keyframes move {
  0% {
    top: 0px;
  }
  20% {
    top: -50px;
  }
  40% {
    top: -100px;
  }
  60% {
    top: -150px;
  }
  80% {
    top: -200px;
  }
}
@keyframes move {
  0% {
    top: 0px;
  }
  20% {
    top: -50px;
  }
  40% {
    top: -100px;
  }
  60% {
